import React from 'react'
import { Link, graphql } from 'gatsby'

import Img from "gatsby-image"

import Layout from '../components/layout'
import SEO from '../components/seo'

const ProductDetailPage = ({data}) => (
  <Layout>
    <SEO title={data.nodeProductDetail.title} />
    <h1>{data.nodeProductDetail.title}</h1>
    <div>
      <Img
        fixed={data.nodeProductDetail.relationships.field_image.localFile.childImageSharp.fixed
        }
      />
    </div>
    <Link to="/">Quay về trang chủ</Link>
  </Layout>
)

export default ProductDetailPage

export const query = graphql`
  query ($slug: String!) {
    nodeProductDetail(fields: {slug: {eq: $slug}}) {
      nid
      title
      relationships {
        field_image {
          url
          localFile {
            childImageSharp {
              fixed(width: 500) {
                ...GatsbyImageSharpFixed
              }
            }
          }
        }
      }
    }
  }
`